import React from "react"
import { fnListConfigApp } from "src/helpers/fnList"
export default function MTCText(props) {
  // console.warn("render MTCText:", props)
  return (
    <div
      className="ani-fadeIn"
      style={{
        color: "red",
        textAlign: "center",
        position: "absolute",
        overflow: "auto",
      }}
    >
      {fnListConfigApp.getIsDevMode() === true && "No coding, contact dev"}
    </div>
  )
}
